function uploadDataToServer(thiz) {
    console.log("uploading data")
    var d = new Date();
    var n = d.getTime();
    const ztate = thiz.state;
    var version = n - thiz.state.participantid;
    var participantid = thiz.state.participantid;
    var broll_list = JSON.stringify(thiz.state.broll_list);
    console.log(broll_list)
    var queries = JSON.stringify({queries: thiz.state.queries});
    var usertype = ztate.usertype
    var uitype = ztate.uitype
    var videotype = ztate.videotype
    const apiURL = 'https://dapptalk.org/bcreate.php';
    const formData = {
      version: version,
      participantid: participantid%10000000,
      broll_list: broll_list,
      queries: queries,
      usertype: usertype,
      uitype: uitype,
      videotype: videotype
    };
    fetch(apiURL, {
      method: 'POST',
      mode: 'no-cors',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin':'*',
      },
      body: JSON.stringify({
        formData,
      })
    }).then((response) => response.text())
      .then((responseData) => { console.log("response: ");console.log(responseData); })
      .catch((err) => { console.log(err); });
  }

