function TranscriptViewVeryIntelligent(props) {
  const thiz = props.context;
  var table = [];
  var words = props.context.state.words;
  var N = words.length;
  var counter_nonbreak = 0;
  var show_recommendations = (props.context.state.uitype === "i");

  const broll_list = props.context.state.broll_list;
  const num_added_broll = broll_list.length;

  var current_recommendation_style = thiz.state.current_recommendation_style;
  var concepts = thiz.state.concepts;

  if (current_recommendation_style === "off"){
    concepts = []
  }
  if (current_recommendation_style === "audio"){
    concepts = thiz.state.concepts_audio
  }
  if (current_recommendation_style === "visual"){
    concepts = thiz.state.concepts_video
  }
  if (current_recommendation_style === "text"){
    concepts = thiz.state.concepts_text
  }
  if (current_recommendation_style === "fast"){
    concepts = thiz.state.concepts_fast
  }
  
  for (var i = 0; i < N; i++) {
    var word = words[i];
    var future_time = parseFloat(words[Math.min(N-1,i+1)].time);
    var prev_time = parseFloat(words[Math.max(0,i-1)].time);
    var current_time = parseFloat(word.time);
    var text = word.name;
    var this_concept = "";
    var currently_in_concept = false;
    var concept_start = 0;
    var suggested_duration = 4000;
    var first_in_concept = false;
    for(let j=0;j<concepts.length;j++) {
      var start_concept = concepts[j][1];
      var end_concept = concepts[j][1] + concepts[j][2];
      if (current_time > start_concept && current_time < end_concept) {
        concept_start = start_concept*1000;
        currently_in_concept = true;
        this_concept = concepts[j][0];
        suggested_duration = concepts[j][2]*1000;
        if (prev_time <= start_concept) {
          first_in_concept = true;
        }
        break
      }
    }
    if (!show_recommendations){
      currently_in_concept = false;
    }
    var currently_in_broll = false;
    var broll_id = 0
    var first_broll_word = false;
    var broll_thumbnail = ""
    var last_broll_word = false;

    for(let j=0;j<num_added_broll;j++){
      var start_broll = broll_list[j].start
      var end_broll = start_broll + broll_list[j].duration/1000
      if (current_time > start_broll && (current_time <= end_broll || prev_time <= start_broll)) {
        broll_thumbnail = broll_list[j].thumbnail
        broll_id = broll_list[j].id;
        currently_in_broll = true;
        if (prev_time <= start_broll) {
          first_broll_word = true;
        }
        if (!(future_time > start_broll && (future_time <= end_broll || current_time <= start_broll))) {
          last_broll_word = true;
        }
        break
      }
    }

    if (!("."===text))
      text = " " + text;

    if(future_time > thiz.state.currentTimeAroll/1000 && current_time <= thiz.state.currentTimeAroll/1000) {
      /*
        CURRENT POSITION
      */  
      if (first_broll_word) {
        //on mouse down it should register current broll index
        //on mouse up it should update the position of the currently registered broll
        table.push(<img src={broll_thumbnail} className="grab" alt="Broll thumbnail" width="20" height="20" draggable='true' onDragStart={thiz.brollDragStartHandler(broll_id)} key={i+"a"}/>)
      }
      table.push(<span key={i+"b"} className="bg-primary text-white text-transcript" onClick={props.context.clickTranscriptIntelligent(current_time*1000,i,"")} onContextMenu={thiz.firstMethod(text)}>{text}</span>);
    } else {
      /*
        B-ROLL
      */  
      if (currently_in_broll){
        if (first_broll_word) {
          //on mouse down it should register current broll index
          //on mouse up it should update the position of the currently registered broll
          table.push(<img src={broll_thumbnail} className="grab-timeline-move" alt="Broll thumbnail" width="20" height="20" draggable='true' onDragStart={thiz.brollDragStartHandler(broll_id)} key={i+"c"}/>)
        }
        if (last_broll_word) {
          table.push(<span className="bg-success text-transcript border-4 border-right border-dark grab-timeline-resize" draggable='true' onDragStart={thiz.brollDragDurationHandler(broll_id)} key={i+"d"}>{text}</span>);
        } else {
          table.push(<span draggable='true' className="bg-success text-transcript" onDragOver={thiz.preventDefault()} onDrop={thiz.brollDropHandler2(current_time,"onlydurationchange",broll_id)} key={i+"e"}>{text}</span>);
        }
      } else if (currently_in_concept){
        table.push(<span className="bg-info text-transcript" data-tip={this_concept} onClick={props.context.getImagesByQueryTerm(this_concept,concept_start/1000,suggested_duration)} draggable='true' onDragOver={thiz.preventDefault()} onDrop={thiz.brollDropHandler(current_time)} key={i+"f"}>{text}</span>);
        if (first_in_concept) {
          table.push(<button className="btn btn-outline-secondary btn-concept" onClick={props.context.getImagesByQueryTerm(this_concept,concept_start/1000,suggested_duration)} key={i+"g"}>{this_concept}</button>)
        }
      } else {   
        /*
          UNHIGHLIGHTED
        */  
        table.push(<span draggable='true' className="text-transcript" onDragOver={thiz.preventDefault()} onDrop={thiz.brollDropHandler(current_time)} onClick={props.context.clickTranscriptIntelligent(current_time*1000,i,"")} onContextMenu={thiz.firstMethod(text)} key={i+"h"}>{text}</span>);
      }
    }
    if (word.name === "."){
      if (counter_nonbreak > 15) {
        table.push(<div className="textbreak" key={i+"i"}></div>);
        counter_nonbreak = 0
      }
    }
    counter_nonbreak += 1;
  }
  return table
}

function TimelineView(props) {
  const thiz = props.context;
  const broll_list = props.context.state.broll_list;
  const num_added_broll = broll_list.length;

  var table = [];
  var words = props.context.state.words;
  var N = words.length;
  if (N < 1) {
    return table;
  }

  var totalDuration = parseFloat(words[N-1].duration) + parseFloat(words[N-1].time);

  var timestep = totalDuration / N;

  for (var i = 0; i < N; i++) {
    var word = words[i];
    var future_time = timestep*(i+1);
    var prev_time = timestep*(Math.max(0,i-1));
    var current_time = timestep*i;

    var currently_in_broll = false;
    var broll_id = 0
    var first_broll_word = false;
    var broll_thumbnail = ""
    var last_broll_word = false;

    for(let j=0;j<num_added_broll;j++){
      var start_broll = broll_list[j].start
      var end_broll = start_broll + broll_list[j].duration/1000
      if (current_time > start_broll && (current_time <= end_broll || prev_time <= start_broll)) {
        broll_id = broll_list[j].id;
        currently_in_broll = true;
        if (current_time-start_broll <= end_broll - current_time) {
          first_broll_word = true;
        } else {
          last_broll_word = true;
        }
        break
      }
    }

    var word_width = 600 * 1 / N;

    /*
      RENDERING STARTS
    */
    if(future_time > thiz.state.currentTimeAroll/1000 && current_time <= thiz.state.currentTimeAroll/1000) {
      /*
        CURRENT POSITION
      */ 
      table.push(<span key={i} className="bg-primary word-timeline" style={{width:word_width}}></span>)
    } else if (currently_in_broll){
      /*
        INSIDE B-ROLL
      */ 
      if (first_broll_word) {
        table.push(<span key={i} className="bg-success word-timeline grab-timeline-move" style={{width:word_width}} draggable='true' onDragStart={thiz.brollDragStartHandler(broll_id)}></span>)
      } else if (last_broll_word) {
        table.push(<span key={i} className="bg-success word-timeline grab-timeline-resize" style={{width:word_width}} draggable='true' onDragStart={thiz.brollDragDurationHandler(broll_id)}></span>);
      } else {
        table.push(<span draggable='true' className="bg-success word-timeline" style={{width:word_width}} onDragOver={thiz.preventDefault()} onDrop={thiz.brollDropHandler2(current_time,"onlydurationchange",broll_id)} key={i}></span>);
      }
    } else {
      /*
        NOTHING
      */ 
      table.push(<span key={i} className="bg-white word-timeline" style={{width:word_width}} draggable='true' onDragOver={thiz.preventDefault()} onDrop={thiz.brollDropHandler(current_time)} onClick={thiz.clickTranscriptIntelligent(current_time*1000,i,"")}></span>)
    }
  }
  return table;
}

function EditingViewBaseline(props) {
	const thiz = props.context;
  var aroll_videofile = "./"+thiz.state.videotype+".mp4"
  var totaltimestamp = "03:05"
  if (document.getElementById("aroll") != null) {
    var dur = document.getElementById("aroll").duration
    totaltimestamp = ("00" + parseInt(dur/60,10)).slice(-2)+":"+("00" + parseInt(dur%60,10)).slice(-2)
  }
  if ("aN:aN" === totaltimestamp) {
    totaltimestamp = "03:05"
  }
	return(
  	<div> 
      {thiz.state.broll_list.map((broll_obj, idx) => (
  			<video width="600" height="360" className="broll" style={{display: 'none'}} id={"broll_"+idx} loop>
  				<source src={broll_obj.url} type="video/mp4"/>
  				Your browser does not support the video tag.
  		  </video>
       ))}
  		<video width="600" height="360" id="aroll" onClick={thiz.arollPlay()} onTimeUpdate={thiz.callingUpdate()} key={aroll_videofile}>
  			<source src={aroll_videofile} type="video/mp4"/>
  			Your browser does not support the video tag.
  		</video>
  	  <div>
    		<button type="button" onClick={thiz.arollPlay()} className="btn btn-secondary left-buffer">Play / Pause</button>
    		<button type="button" onClick={thiz.arollReplay()} className="btn btn-secondary">Replay</button>
        <span className="left-buffer top-buffer-sm">{thiz.state.timestamp}/{totaltimestamp}</span>
  	  </div>
  	</div>
	);
}

function RenderBrollResultIntelligent(props) {
    const thiz = props.context;
    const type = props.type;
    var table1 = []
    var broll_footage = thiz.state.broll_footage.social;
    var broll_thumbnail = thiz.state.broll_thumbnail.social;
    if (type === "professional") {
      var broll_footage = thiz.state.broll_footage.professional;
      var broll_thumbnail = thiz.state.broll_thumbnail.professional;
    }
    var N = broll_footage.length;
    const maxindex = Math.min(N,thiz.state.maxindex);
    if (maxindex < 1) {
      return table1
    }
    var per_row = 2;
    // Outer loop to creaSearte parent
    for (var i = 0; i < maxindex; i+=per_row) {
      var children = []
      //Inner loop to create children
      for (let j = 0; j < per_row; j++) {
        children.push(
          <div className="col-6">
          <video width="200" height="200" key={broll_footage[i+j]} name="broll" controls autoPlay loop muted>
            <source src={broll_footage[i + j]} type="video/mp4"/>
            Your browser does not support the video tag.
          </video>
          <button onClick={thiz.addNow(broll_footage[i + j],broll_thumbnail[i+j],i+j)} className="btn btn-primary">Add Now</button>
        </div>
          )
      }
      //Create the parent and add the children
      table1.push(<div key={i} className="row top-buffer">{children}</div>)
    }
    return table1
}

function showThisBroll (name,duration) {
	if(document.readyState === "complete") {
  	$("#"+name).show();
  	document.getElementById(name).currentTime=0;
  	document.getElementById(name).play();
  	$("#aroll").hide();
  	setTimeout(function(){
  		$("#"+name).hide();
  		$("#aroll").show();
  	}, duration);
	}
}

